import emotions from '~/assets/emotionList.js'
export default {
  name: 'EmoticonTextarea',
  props: {
    content: {
      type: String,
      default: ''
    }
  },
  data() {
    return {}
  },
  computed: {
    html() {
      return this.content
        .replace(/&/g, '&amp;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/"/g, '&quot;')
        .replace(/'/g, '&apos;')
        .replace(/\r?\n/g, '<br>')
        .replace(/\s/g, '&nbsp;')
        .replace(/\\/g, '&#x27;')
        .replace(/\//g, '&#x2F;')
        .replace(/\[[\u4E00-\u9FA5]{1,3}\]/gi, (words) => {
          const word = words.replace(/\[|\]/gi, '')
          console.log(word)
          const index = emotions.emotionList.indexOf(word)
          if (index !== -1) {
            return `<img src="https://res.wx.qq.com/mpres/htmledition/images/icon/emotion/${index}.gif">`
          } else {
            return words
          }
        })
    }
  },
  created() {},
  mounted() {},
  methods: {}
}
